html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

body {
  --nav-height: 60px;
  background-color: #dcd7d7;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 1200px;
  margin: auto;
  display: flex;
}

@media (width <= 1024px) {
  .header {
    width: 1024px;
  }
}

.nav__link {
  cursor: pointer;
  color: #000;
  height: var(--nav-height);
  line-height: var(--nav-height);
  margin-left: 20px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  margin: 0;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 50px;
  display: block;
  position: relative;
  bottom: 4px;
}

.logo {
  margin-left: 50px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav {
  margin-right: 50px;
  font-size: 12px;
  font-weight: 500;
}

[data-qa="hover"]:hover {
  color: #00acdc;
}

